const Mock = require('mockjs');
import BASE_INFO from '../../static/config.js'

const base = BASE_INFO.BASE_URL;
/**/
Mock.mock(base + 'api/resource/getTourRuleSet', "post",function(params){
  params = params.body;
  params = JSON.parse(params);
  /*
   * @param devId ID
   * */
  var data = {
    "code": 0,
    "data":Mock.mock({
      "data|1-30": [
        {
          "devType|+1": [
            "",
            ""
          ],
          "devModel|+1": [
            "S13",
            "S16"
          ],
          "serviceCycle|+1": [
            "3",
            "5"
          ]
        }
      ]
    }),
    "msg": "success"
  }
  return data;
});
