Mock.mock(base + 'api/resource/getMonthDefectAny', "post",function(params){
  params = params.body;
  params = JSON.parse(params);
  /*
   * @param monthDate 
   * */
  let firstGradeArr = [];
  let secondGradeArr = [];
  let thirdGeadeArr = [];
  for (let i = 1; i <= 31; i++) {
    firstGradeArr.push(Mock.Random.integer(0, 1000));
    secondGradeArr.push(Mock.Random.integer(0, 1000));
    thirdGeadeArr.push(Mock.Random.integer(0, 1000));
  }
  var data = {
    "code": 0,
    "data":[
      {
        "id":1,
        "name": "",
        "data": firstGradeArr
      },{
        "id":2,
        "name": "",
        "data": secondGradeArr
      },{
        "id":3,
        "name": "",
        "data": thirdGeadeArr
      }
    ],
    "msg": "success"
  };
  return data;
});




