const Mock = require('mockjs');
import BASE_INFO from '../../static/config.js'

const base = BASE_INFO.BASE_URL;

Mock.mock(base + 'api/user/getUserInfo', "get", {
  "code": 0,
  "data": {
    "fullName": "@CNAME", // 
    "userId": 1000234234001,
    "username": "zhangsan"
  },
  "msg": "success"
})






Mock.mock(base + 'api/users', "get", {
  "code": 0,
  'tableData|100': [{
    date: () => Mock.Random.datetime(),
    sf: () => Mock.Random.float(10, 60),
    yc: () => Mock.Random.float(20, 50),
    jd: () => Mock.Random.integer(5, 10) + "%",
    zxs: () => Mock.Random.float(30, 40),
    zxx: () => Mock.Random.float(40, 50),
    xd: () => Mock.Random.float(50, 60),
  }],
  "msg": "success"
})



Mock.mock(base + 'api/lineView', "get", {
  "code": 0,
  data:{
    one: (function(){
      let arr=[];
       for(let i=0;i<96;i++){
        arr.push(Mock.Random.integer(0, 100));
       }
       return arr;
    })(),
    two:(function(){
      let arr=[];
       for(let i=0;i<96;i++){
        arr.push(Mock.Random.integer(0, 100));
       }
       return arr;
    })()
  },
  "msg": "success"
})






