Mock.mock(base + 'api/resource/getresourcelistBytpe', "post", function(params){
    params=params.body;
    params=JSON.parse(params);
    var type=params.type;//
    console.log("type:"+type);
    var typelist=['gf','fd','hd','sd'];
    var data=[];
    var len=type?20:40;
    for(var i=0;i<len;i++){
        var name=Mock.mock('@CNAME'); 
        var point=lonlatlist[i];
        var lon=Number(point[0])+Math.random();
        var lat=Number(point[1])+Math.random();
        var types=type?type:typelist.Random;

        data.push({
            'type':types,
            'stationid':Mock.mock('@id'),//id
            'name':name+'',// 
            'capacity':Mock.Random.integer(10, 1000),
            'maxcapacity':Mock.Random.integer(10, 1000),
            'capacity2':Mock.Random.integer(10, 1000),
            'maxcapacity2':Mock.Random.integer(10, 1000),
            
            'stationpower':Mock.Random.integer(0, 1000),//  
            'daystations':Mock.Random.integer(0, 1000),//
            'stationpr':Mock.Random.integer(0, 100),//pr
            'equaltime':Mock.Random.float(0, 10, 1, 1),//
            'unconfirm':Mock.Random.integer(0, 10),//
            'undeal':Mock.Random.integer(0, 10),//
            'dealing':Mock.Random.integer(0, 10),//
            'lon':lon,//
            'lat':lat//
        });
    }


    return {
        "code": 0,
        "data":data,
        "msg": "success"

    };

});

