/* echart*/
Mock.mock(base + 'api/cost/getstandanalysischart', "post", function(params){
    params=params.body;
    params=JSON.parse(params);
    var nh=[],cost=[],provinciallevel=[],enterpriselevel=[],xdata=[];
  
    if(params.datetype=='year'){
        for(var i=1;i<=12;i++){//
           nh.push(Mock.Random.float(0, 100, 1, 1));
           cost.push(Mock.Random.float(0, 100, 1, 1));
           provinciallevel.push(Mock.Random.float(0, 100, 1, 1));
           enterpriselevel.push(Mock.Random.float(0, 100, 1, 1));
           xdata.push(i+'');
        }

    }else if(params.datetype=='month'){
        var splitstr=params.date.split('-');
        var daynum=new Date(splitstr[0],splitstr[1],0).getDate();//
        for(var i=1;i<=daynum;i++){
            nh.push(Mock.Random.float(0, 100, 1, 1));
            cost.push(Mock.Random.float(0, 100, 1, 1));
            provinciallevel.push(Mock.Random.float(0, 100, 1, 1));
            enterpriselevel.push(Mock.Random.float(0, 100, 1, 1));
            xdata.push(i);
        }
    }else if(params.datetype=='day'){

        var startdate=params.date[0];
        var enddate=params.date[1];
        var d1=new Date(startdate);
        var d2=new Date(enddate);
        var a=(d2-d1)/(1000 * 60 * 60 * 24);
        if(a==0){//
           for(var i=1;i<=24;i++){
              nh.push(Mock.Random.float(0, 100, 1, 1));
              cost.push(Mock.Random.float(0, 100, 1, 1));
              provinciallevel.push(Mock.Random.float(0, 100, 1, 1));
              enterpriselevel.push(Mock.Random.float(0, 100, 1, 1));
               var s=i<10?'0'+i:i;
               xdata.push(s+' 00:00');
           }
        }else{
            var substartdate=startdate.substring(0,10);
            nh.push(Mock.Random.float(0, 100, 1, 1));
            cost.push(Mock.Random.float(0, 100, 1, 1));
            provinciallevel.push(Mock.Random.float(0, 100, 1, 1));
            enterpriselevel.push(Mock.Random.float(0, 100, 1, 1));
            xdata.push(substartdate);

            var subenddate=enddate.substring(0,10);
            nh.push(Mock.Random.float(0, 100, 1, 1));
            cost.push(Mock.Random.float(0, 100, 1, 1));
            provinciallevel.push(Mock.Random.float(0, 100, 1, 1));
            enterpriselevel.push(Mock.Random.float(0, 100, 1, 1));
            xdata.push(subenddate);

            
            for(var i=1;i<a;i++){
                var date=getDaybycount(substartdate,i);
                nh.push(Mock.Random.float(0, 100, 1, 1));
                cost.push(Mock.Random.float(0, 100, 1, 1));
                provinciallevel.push(Mock.Random.float(0, 100, 1, 1));
                enterpriselevel.push(Mock.Random.float(0, 100, 1, 1));
                xdata.push(date);

            }


        }


    }
    

    return {
        "code": 0,
        "data":{
            nh:nh,
            cost:cost,
            provinciallevel:provinciallevel,
            enterpriselevel:enterpriselevel,
            xdata:xdata

        },
        "msg": "success"

    };
});
function getDaybycount(startday,daynum){
     var today = new Date(startday);    
    var targetday_milliseconds=today.getTime() + 1000*60*60*24*daynum;            
    today.setTime(targetday_milliseconds); //         
    var tYear = today.getFullYear();    
    var tMonth = today.getMonth();    
    var tDate = today.getDate();   
    tMonth= tMonth + 1;
    tMonth =tMonth<10?'0'+tMonth:tMonth;    
    tDate =tDate<10?'0'+tDate:tDate;   
    return tYear+"-"+tMonth+"-"+tDate; 

}


