import axios from 'axios'
import qs from 'qs'


export default {
    //get
    get(url, param) {
        return new Promise((resolve, reject) => {
            axios({
                method: 'get',
                headers:{"Content-Type":'application/x-www-form-urlencoded; charset=UTF-8'},
                url,
                params: param
            }).then(res => {
                resolve(res)
            })
        })
    },
    //post
    post(url, param) {
        return new Promise((resolve, reject) => {
            axios({
                method: 'post',
                url,
                data: param
            }).then(res => {
                resolve(res)
            })
        })
    }
}


