const Mock = require('mockjs');
import BASE_INFO from '../../static/config.js'

const base = BASE_INFO.BASE_URL;
/**/
Mock.mock(base + 'api/cost/getPVCosts', "get", {
  "code": 0,
  "data": [
    {
      "devId": 20376583,
      "unitType": "",
      "ratedPower":"500",
      "chargeEfficiency":"@integer(0, 100)",
      "PVPrice":"@float(0, 50, 1, 2)",
      "cost":"@integer(0, 600)",
      data: function () {
        let arr = [];
        for (let i = 1; i <= 12; i++) {
          arr.push({"time": i * 10, "value": Mock.Random.integer(0, 100000)});
        }
        return arr;
      }
    }
  ],
  "msg": "success"
});
