/**/
Mock.mock(base + 'api/cost/getForecastResult', "post", function(parms){
  parms = parms.body;
  parms = JSON.parse(parms);
  //console.log(parseInt(parms.days) * 24 * 60 / 4);
  var actualArr = [];
  var forecastArr = [];
  for (let i = 1; i <= (parseInt(parms.days) + 1) * 24 * 4 - 1; i++) {
    actualArr.push(Mock.Random.integer(0, 1000));
    forecastArr.push( Mock.Random.integer(0, 1000));
  }
  var data = {
    "code": 0,
    "data":{
      "actualArr":actualArr,
      "forecastArr":forecastArr
    },
    "msg": "success"
  };
  return data;
});
/**/
Mock.mock(base + 'api/cost/getForecastError', "post", function(parms){
  parms = parms.body;
  parms = JSON.parse(parms);
  //console.log(parseInt(parms.days) * 24 * 60 / 4);
  var errorArr = [];
  for (let i = 1; i <= (parseInt(parms.days) + 1) * 24 * 4 - 1; i++) {
    errorArr.push(Mock.Random.integer(0, 100000));
  }
  var data = {
    "code": 0,
    "data":{
      "errorArr":errorArr,
    },
    "msg": "success"
  };
  return data;
});
