Vue.selfencrypt = function(strValue) { //
    try {
        var strLength = "";
        var strEncrypt = "";
        // 
        for (var nIndex = 0; nIndex < strValue.length; ++nIndex) {
            var strUnicode = strValue.charCodeAt(nIndex).toString(); // Unicode
            if (strUnicode == "") {
                continue;
            }

            // Unicode
            if (strLength == "") {
                strLength += strUnicode.length.toString();
            } else {
                strLength += "," + strUnicode.length.toString();
            }

            // Unicode
            for (var nIndex2 = 0; nIndex2 < strUnicode.length; ++nIndex2) {
                var strPer = strUnicode[nIndex2];
                strPer ^= 0xFFF; // 
                strEncrypt += strPer;
            }
        }

        var jsonEncrypt = {
            LENGTH: strLength,
            ENCRYPT: Base64.encode(strEncrypt)
        }

        return jsonEncrypt;

    } catch (e) {
        return null;
    }


}
Vue.selfdecrypt = function(jsonEncrypt) { //
    try {
        var strResult = "";
        var arrLength = jsonEncrypt.LENGTH.split(",");
        var strDecrypt = Base64.decode(jsonEncrypt.ENCRYPT);
        // 
        var strPer = "";
        for (var nIndex = 0; nIndex < strDecrypt.length; ++nIndex) {
            strPer += strDecrypt[nIndex];
            if (strPer.length == 4) {
                strResult += strPer ^ 0xFFF; // 
                strPer = "";
            }
        }

        // Unicode
        var nCurIndex = 0;
        strDecrypt = "";
        for (var nIndex = 0; nIndex < strResult.length; ++nIndex) {
            strPer += strResult[nIndex];
            if (strPer.length == arrLength[nCurIndex]) {
                strDecrypt += String.fromCharCode(strPer);
                nCurIndex += 1;
                strPer = "";
            }
        }

        return strDecrypt;

    } catch (e) {
        return null;
    }


}

Vue.setCookie = function(cookiename, cookievalue, days) { //cookie  //
    try {
        var d = new Date;
        d.setTime(d.getTime() + 60*24 * 60 * 1000 * days); //60*24,days1
        cookievalue = Vue.selfencrypt(cookievalue);
        if (cookievalue)
            cookievalue = JSON.stringify(cookievalue);
        window.document.cookie = cookiename + "=" + cookievalue + ";path=/;expires=" + d.toGMTString();

    } catch (e) {console.log('cookie');console.dir(e);}

};
Vue.getCookie = function(cookiename, nodec) {
    try {
        var v = window.document.cookie.match('(^|;) ?' + cookiename + '=([^;]*)(;|$)');
        var vn = v ? v[2] : null;

        if (vn) {
            if (nodec) {
                return vn;
            } else {
                vn = JSON.parse(vn);
                vn = Vue.selfdecrypt(vn);
                return vn;
            }

        } else
            return null;

    } catch (e) {
        return null;
    }


};
Vue.deleteCookie = function(cookiename) {
    var days = 0;
    var d = new Date;
    d.setTime(d.getTime() + 0);
    window.document.cookie = cookiename + "=" + '' + ";path=/;expires=" + d.toGMTString();

};
Vue.trim = function(str) { //
    var s = /^[\s\uFEFF\xA0]+|[\s\uFEFF\xA0]+$/g;
    return null == str ? "" : (str + "").replace(s, "")
}
