Mock.mock(base + 'api/resource/getServicePlanAny', "post",function(params){
  params = params.body;
  params = JSON.parse(params);
  /*
   * @param monthDate 
   * */
  let planArr = [];
  for (let i = 1; i <= 31; i++) {
    planArr.push(Mock.Random.integer(0, 1000));
  }
  var data = {
    "code": 0,
    "data":[
      {
        "id":1,
        "name": "",
        "data": planArr
      }
    ],
    "msg": "success"
  };
  return data;
});
